* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  display: flex;

  align-items: center;
  justify-content: center;
}

body, html {
  height: 100%;
  width: 100%;
}

.example-application__container {
  margin-bottom: 40px;
}

.example-application__pages {
  text-align: center;
  opacity: 0.25;
  -webkit-transition:  0.5s;
  transition:  0.5s;
}

.example-application__pages:hover {
  opacity: 1;
}

.example-application__page {
  cursor: pointer;
  font-weight: bold;
  opacity: 0.4;
  -webkit-transition:  0.5s;
  transition:  0.5s;
  padding: 10px;
}

.example-application__page--active {
  opacity: 1;
}


.centered-block {
  position: relative;
}
.centered-block__man {
  position: absolute;
  top: -9px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  pointer-events: none;
}
.centered-block__container {
  opacity: 1;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
  padding-bottom: 90px;
}

.centered-block__container--dragging {
  opacity: 0.95;
}

.scroll-container {
  height: 145px;
  width: 430px;
  background: #282828;
  border: solid 5px #E2D7C7;
}

.hieroglyphs {
  height: 100%;
  display: flex;
  align-items: center;
  padding: 20px;
  width: 900px;
  justify-content: space-between;
}

.hieroglyph {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
  opacity: 1;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}

.hieroglyph--hidden {
    opacity: 0.2;
}

.scrollbars-example {
  border: solid 5px #E2D7C7;
  background: #282828;
  height: 310px;
  width: 310px;
  overflow: hidden;
}

.scrollbars-example__container {
  height: 100%;
  width: 100%;
  overflow: auto;
}

.scrollbars-example__row {
  display: flex;
}

.scrollbars-example__col {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #282828;
  color: #E2D7C7;
  width: 50px;
  height: 50px;
  flex-shrink: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-weight: bold;
  font-size: 25px;
}

.image-example {
  border: solid 5px #E2D7C7;
  background: #282828;
  height: 310px;
  width: 310px;
  overflow: hidden;
}

.image-example__container {
  height: 100%;
  width: 100%;
}

.image-example__image {
  display: block;
}

